<template><section><h3>demo Alert 组件提供四种主题，由<code>type</code>属性指定，默认值为<code>info</code>。</h3>
<template>
    <y-button type="ghost" @click.native="test1">test</y-button>
    <y-button type="ghost" @click.native="test2">带有icon</y-button>
    <y-button type="ghost" @click.native="test3">带有icon，自动消失</y-button>
    <y-button type="ghost" @click.native="test4">带有icon</y-button>
    <y-button type="ghost" @click.native="test5">带有icon</y-button>
    <y-button type="ghost" @click.native="test6">带有icon</y-button>
    <y-button type="ghost" @click.native="test7">带有icon,关闭回调</y-button>
</template>
  
<h2>用法</h2>
<pre><code class="language-javacript">this.$notify({
    &quot;type&quot;: &quot;danger&quot;,
    &quot;title&quot;:&quot;test&quot;,
    &quot;content&quot;:&quot;sfafasdf&quot;,
    &quot;icon&quot;:true,
    &quot;onClose&quot;:()=&gt;{
        console.log(1)
    }
})
</code></pre>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型</td>
<td><code>String</code></td>
<td><code>success</code></td>
<td><code>info</code>,<code>warning</code>,<code>danger</code></td>
</tr>
<tr>
<td>title</td>
<td>标题</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td>内容</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>onClose</td>
<td>关闭时的回调</td>
<td><code>Function</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>icon</td>
<td>icon</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
<tr>
<td>time</td>
<td>延迟关闭时间</td>
<td><code>Number</code></td>
<td><code>0(默认不关闭,毫秒)</code></td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    methods:{
        test1(){
            this.$notify("test")
        },
        test2(){
            this.$notify({
                "title":"test",
                "content":"sfafasdf",
                "icon":true
            })
        },
        test3(){
            this.$notify({
                "title":"test",
                "content":"sfafasdf",
                "icon":true, 
                "time":1000
            })
        },
        test4(){
            this.$notify({
                "type": "success",
                "title":"test",
                "content":"sfafasdf",
                "icon":true
            })
        },
        test5(){
            this.$notify({
                "type": "info",
                "title":"test",
                "content":"sfafasdf",
                "icon":true
            })
        },
        test6(){
            this.$notify({
                "type": "warning",
                "title":"test",
                "content":"sfafasdf",
                "icon":true
            })
        },
        test7(){
            this.$notify({
                "type": "danger",
                "title":"test",
                "content":"sfafasdf",
                "icon":true
            })
        },
        test7(){
            this.$notify({
                "type": "danger",
                "title":"test",
                "content":"sfafasdf",
                "icon":true,
                "onClose":()=>{
                    console.log(1)
                }
            })
        }
    }
}
</script>


