<template><section><h1>modal</h1>
<template>
    <y-button type="primary" @click.native="test">sss</y-button>
</template>

<h2>用法</h2>
<pre><code class="language-javacript">this.$modal({
    &quot;title&quot;:&quot;teststs&quot;,
    &quot;content&quot;:&quot;你好呀世界&quot;,
}).then((value)=&gt;{      // 确认回调
    this.$notify(&quot;确认&quot;)
}).catch((value)=&gt;{     // 取消回调
    this.$notify(&quot;取消&quot;)
})
</code></pre>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td>内容</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>vis</td>
<td>按钮事件是否默认关闭</td>
<td><code>Boolean</code></td>
<td><code>true</code></td>
<td>-</td>
</tr>
<tr>
<td>okBtn</td>
<td>右边按钮选项</td>
<td><code>Object</code></td>
<td><code>{show:true,text:&quot;确认&quot;}</code></td>
<td>-</td>
</tr>
<tr>
<td>backBtn</td>
<td>左边按钮选项</td>
<td><code>Object</code></td>
<td><code>{show:true,text:&quot;取消&quot;}</code></td>
<td>-</td>
</tr>
<tr>
<td>.then</td>
<td>右边按钮回调事件</td>
<td><code>Function</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>.catch</td>
<td>右边按钮回调时间</td>
<td><code>Function</code></td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    methods:{
        test(){
            this.$modal({
                "title":"teststs",
                "content":"你好呀世界",
            }).then((value)=>{
                this.$notify("确认")
            }).catch((value)=>{
                this.$notify("取消")
            })
        }
    }
}
</script>


